











import { Vue, Component } from 'vue-property-decorator'
@Component
export default class Sort extends Vue {
  private options: Array<object> = [
    {
      label: 'Rating',
      value: 'rating'
    },
    {
      label: 'Cost',
      value: 'cost'
    }
  ]
  private selectedOption: any = {
    label: 'Rating',
    value: 'rating'
  };

  public updatedOption(val: object): void {
    this.$emit('sort', val);
  }
}
