











import { Vue, Component } from 'vue-property-decorator'
@Component
export default class Order extends Vue {
  private options: Array<object> = [
    {
      label: 'Ascending',
      value: 'asc'
    },
    {
      label: 'Descending',
      value: 'desc'
    }
  ]
  private selectedOption: any =     {
    label: 'Ascending',
    value: 'asc'
  };

  public updatedOption(val: object): void {
    this.$emit('order', val);
  }
}
